import './App.css';
import {
  BrowserRouter, 
  Switch, 
  Route,
} from "react-router-dom";
import Header from "./blog/sections/header";
import Blogpage from './blog';
import BlogPost from './blog/components/blogpost';

function App() {
  return (
    <div className="App container">
        <BrowserRouter>
        <Header></Header>
          
          <Switch>
            <Route path={`/blogpost/:postId`}>
              <BlogPost />
            </Route>
            
            <Route path="/">
              <Blogpage />
            </Route>
          </Switch>

        </BrowserRouter>
    </div>
  );
}

export default App;
