const Post2 = () => {
    return (
        <article className="mt-3">
            <div className="bg-secondary p-3 m-3">
                <h4>Prerequisites</h4>
                <h5>Create a new repository on github and keep note of repository url</h5>
            </div>
            <div> 
                <h2>Intializing a repository from folder</h2>
                <p>To initialize a new repository we can use two commands</p>
                <ul>
                    <li>using git init</li>
                    <li>git clone</li>
                </ul>
                
                <h4>Using git clone</h4>
                <code>
                    git clone ${"{ repository_url }"}
                </code>
                <p>Using command you can download a repository from a remote url into your local machine</p>

                <h4>Using git init</h4>
                <code>
                    git init
                </code>
                <p>To initialize a repository, Git creates a hidden directory called <em>.git</em> . This <em>.git</em> directory stores all of the objects and refs that Git uses and creates as a part of your project's history.</p>
                <h4>Connect initialized repository to remote url</h4>
                <code>git remote add origin {"{ repository_url }"}</code>
            </div>

            <div>
                <h2>Commiting changes to repository</h2>
                <h4>Add files to staging area</h4>
                <code>
                    # Add particular file to staging area
                    <br />
                    git add {"${ file1.txt }"} {"${ file2.txt }"}
                    <br />
                    <br />
                    # To add all files in that directory
                    <br />
                    git add .   
                </code>

                <h4>To Check status of added files</h4>
                <code>git status</code>

                <h4>Commit changes</h4>
                <code>git commit -m "Commit message"</code>

                <h4>Push code</h4>
                <code>
                    # Push to master branch
                    <br />
                    git push origin master
                    <br />
                    <br />
                    # Push to a particular branch
                    <br />
                    git push origin {"{ branch_name }"}
                </code>
            </div>
            
            <div>
                <h2>Pulling code from a branch</h2>
                <code>
                    # Pull from master branch
                    <br />
                    git pull origin master
                    <br />
                    <br />
                    # Push to a particular branch
                    <br />
                    git pull origin {"{ branch_name }"}
                </code>
            </div>

            <div>
                <h2>Commands on Git Branch</h2>
                <h4>Creates a new branch and switches to newly created branch </h4>
                <code>git checkout -b {"{ branch_name }"}</code>

                <h4>Switch to another branch</h4>
                <code>git checkout {"{ branch_name }"}</code>

                <h4>List all branches</h4>
                <code>git branch</code>
                <p>Green text indicates you are currently on that branch</p>
            </div>
        </article>
    );
}

export default Post2;